  checkUrl() {

    this.items.forEach(i => {
      i.active = false

      if (i.routerLink == '' && document.location.pathname == '') {
        i.active = true;
      }
      else if (i.routerLink != '' && document.location.pathname.toString().toLowerCase().indexOf(i.routerLink) > -1) {
        i.active = true;
      }
    });
  }

