  prepareRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData && outlet.activatedRouteData['animation'];
  }

  
  setUpNotifier() { 
    this.apiRequestService.onMessage$.subscribe((res: ResponseObject<any>) => {
    if (res.Data != null && res.Data.status != 0 && res.Message != null && res.Message.trim() != "") {
      
      this.checkCounterForSimilarMessage(res, () => {
        this.notifierService.notify(
          res.Result == Result.Success ? 'success' :
            ((res.Result == Result.Failure || res.Result == Result.ValidationError || res.Result == Result.Unauthorized) ? 'warning'
              : 'error')
          , res.Message);
          if(res.Data != null && res.Data.callback != null) {
            res.Data.callback();
          }
        });
      }
    })
  }

  checkCounterForSimilarMessage(res, callBack?: any) {
    let existingIndex = this.currentNotification.findIndex(i => i.Message = res.Message && i.Result == res.Result);
    if (existingIndex > -1) {
      setTimeout(() => {
        if (existingIndex < this.currentNotification.length)
          this.currentNotification.splice(existingIndex, 1);
      }, 2000);
    } else {
      this.currentNotification.push(Object.assign({}, res));
      callBack();
    }
    return false;
  }
