import { AfterViewInit, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { debounceTime, finalize, switchMap, tap } from 'rxjs/operators';
import { ResponseObject } from 'src/app/models/common';
import { Product, ProductFilter } from 'src/app/models/product';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { environment } from 'src/environments/environment';

declare var UIkit: any;

@Component({
  selector: 'app-search-view',
  templateUrl: './search-view.component.html',
  styleUrls: ['./search-view.component.scss']
})
export class SearchViewComponent implements OnInit, AfterViewInit {

  @ViewChild("searchBox") private searchElement: ElementRef;

  formInstance: FormGroup = this.fb.group({
    Search: ['']
  })

  get searchCtrl(): AbstractControl | null {
    return this.formInstance.get("Search");
  }

  get recentSearches(): string[] {
    let searches = <string[]>JSON.parse(localStorage.getItem("recentSearches") ?? "[]");
    return searches;
  }

  set recentSearches(searches: string[]) {
    localStorage.setItem("recentSearches", JSON.stringify(searches));
  }

  addToRecentSearches(searchText: string) {
    let searches = this.recentSearches;
    if (searches.length > 2) {
      searches.splice(searches.length -1, 1);
    }
    if (searches != null && searches.findIndex(j => j.toLowerCase() == searchText.toLowerCase()) == -1) {
      searches = [searchText, ...searches];
      this.recentSearches = searches;
    }
  }

  productList: Product[] = [];

  isLoading: boolean = false;

  errorMsg: string = "";

  productFilter: ProductFilter = new ProductFilter();

  uploadsUrl: string = environment.uploadsUrl;

  constructor(private productService: ProductsService, private router: Router,
    private fb: FormBuilder) { }

  ngOnInit(): void {

    this.productFilter.Search = "";
    this.productFilter.PageSize = 10;

    this.searchCtrl?.valueChanges
      .pipe(
        debounceTime(1000),
        tap(() => {
          this.errorMsg = "";
          this.productList = [];
          this.isLoading = true;
        }),
        switchMap(value => {

          this.productFilter.Search = value
          this.productFilter.PageSize = 5;
          return this.productService.getProductList(this.productFilter)
            .pipe(
              finalize(() => {
                this.isLoading = false
              }),
            )
        })
      )
      .subscribe((res: ResponseObject<Product[]>) => {
        if (res.Data == undefined) {
          // this.errorMsg = data['Error'];
          this.productList = [];
        } else {
          this.errorMsg = "";
          this.productList = this.processProductList(res.Data);
        }

        console.log("Search Result: " + this.productList);
      });
  }


  ngAfterViewInit() {
    // Otherwise Angular throws error: Expression has changed after it was checked.
    window.setTimeout(() => {
      this.searchElement.nativeElement.focus();
    });
  }


  processProductList(list: Product[]): Product[] {
    let _res: Product[] = []
    list.forEach(item => {
      let images: string[] = [];
      item.ProductImages.forEach(img => {
        images.push(img.replace('~', ''));
      });
      item.ProductImages = images
      _res.push(item);
    });
    return _res;
  }

  searchPerformed(res?: string) {
    let q = res;
    if (q == null) {
      q = this.searchCtrl?.value;
    }
    if(q == "") return;
    if (q != null) this.addToRecentSearches(q);
    this.router.navigateByUrl('/ps?q=' + q)
  }
}
