import { Component, Injector, OnInit } from '@angular/core';
import { ResponseObject } from 'src/app/models/common';
import { OfferBanner, OfferFilter, OfferBannerPlacement } from 'src/app/models/offer';
import { OfferService } from 'src/app/services/api-services/offers.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-home-stories-offers',
  templateUrl: './mb-home-stories-offers.component.html',
  styleUrls: ['./mb-home-stories-offers.component.scss']
})
export class MbHomeStoriesOffersComponent extends BaseComponent implements OnInit {

  uploadsUrl: string = environment.uploadsUrl;

  offerBanners: OfferBanner[] = [];

  constructor(protected injector: Injector, protected offerSeervice: OfferService) {
    super(injector);

  }

  ngOnInit(): void {

    let filter = new OfferFilter();
    this.offerSeervice.getOfferBannerList(filter).subscribe((res: ResponseObject<OfferBanner[]>) => {
      res.Data?.forEach((item: OfferBanner) => {
        item.BannerImageUrl = item.BannerImageUrl.replace("~/", '');
      })
      this.offerBanners = (res.Data ?? [])//.filter(i => i.Placement == OfferBannerPlacement.MobileStatus);
    })
  }

  navigateViewStory(offerBanner: OfferBanner) {
    this.offerSeervice.changeCurrentBannerr(offerBanner);
    this.router.navigate(["/view-story"]);
  }
}
