import { Component, Injector, Input, OnInit } from '@angular/core';
import { BaseComponent } from 'src/app/shared/base.component';
import { Wallet } from 'src/app/models/wallet';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { WalletService } from 'src/app/services/api-services/wallet.service';

@Component({
  selector: 'app-mb-my-wallet',
  templateUrl: './mb-my-wallet.component.html',
  styleUrls: ['./mb-my-wallet.component.scss']
})
export class MbMyWalletComponent extends BaseComponent implements OnInit {
   
  walletList: Wallet[];
  currentBalance: number;
  
  constructor(protected injector: Injector, private authService: AuthenticationService, private walletService: WalletService) {
    super(injector);
  }

  ngOnInit(): void {
 
    this.loadWalletInfo();
  }

  loadWalletInfo() {
    this.walletService.getWalletList().subscribe((res: { Data: Wallet[]; }) => {
      this.walletList = res.Data;
      this.currentBalance = 0;
      this.walletList.forEach(item => this.currentBalance += item.Amount);
    })
  }

}
