import { Component, Injector, Input, OnInit } from '@angular/core';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { Cart, CartProductView } from 'src/app/models/cart';
import { ResponseObject } from 'src/app/models/common';
import { ProductFilter, ProductVariant } from 'src/app/models/product';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { CartService } from 'src/app/services/cart.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.scss']
})
export class CartComponent extends BaseComponent implements OnInit {

  productCartList: CartProductView[] = [];
  assetsUrl: string = environment.assetsUrl;
  imgUrl: string = environment.uploadsUrl;

  @Input() cartId: string;

  get cartList(): Cart[] {
    return this.cartService.getCartList()
  }

  get cartTotal(): number {
    return this.productCartList.reduce((sum, item) => sum + (item.Quantity * item.ProductVariant.Price), 0);
  }


  constructor(public injector: Injector, private cartService: CartService,
    private authService: AuthenticationService,
    private productService: ProductsService) {
    super(injector);
  }


  ngOnInit(): void {

    this.loadProducts();

    this.cartService.cartUpdated.subscribe((cartItem: Cart) => {
      if (cartItem == null || cartItem == undefined) {
        this.loadProducts();
      } else {
        let index = this.productCartList.findIndex(i => i.ProductVariantId == cartItem.ProductVariantId);
        if (index > -1) {
          if (cartItem.Quantity == 0) {
            this.productCartList.splice(index, 1);
          }
          else {
            this.productCartList[index].Quantity = cartItem.Quantity;
          }
        } else {
          this.loadProducts();
        }
      }
    });
  }

  loadProducts() {

    if (this.cartList.length == 0) {
      this.productCartList = [];
      return;
    }

    this.showSpinner();

    let filter: ProductFilter = new ProductFilter();
    filter.Ids = this.cartList.map(i => i.ProductVariantId).join(',');
    this.productService.getProductVariantList(filter).subscribe((res: ResponseObject<ProductVariant[]>) => {
      let productCartList: CartProductView[] = [];
      let variantList = res.Data ?? [];
      variantList.forEach(vItem => {
        let len = vItem?.Product?.ProductImages?.length ?? 0;
        for (let i = 0; i < len; i++) {
          let arr = (vItem?.Product?.ProductImages ?? []);
          let str = arr[i].replace('~', '');
          if (vItem?.Product?.ProductImages != null) {
            vItem.Product.ProductImages[i] = str;
          }
        }
        this.cartList.forEach(cItem => {
          if (vItem.Id == cItem.ProductVariantId) {
            productCartList.push({
              Quantity: cItem.Quantity,
              ProductVariantId: cItem.ProductVariantId,
              ProductVariant: vItem
            })
          }
        });
      })
      this.productCartList = productCartList;
      this.hideSpinner();
    });
  }

  removeProduct(id: number) {
    this.cartService.removeItem(id);
  }

  addToCart(id: number) {
    this.cartService.addItem(id);
  }

  decreaseFromCart(id: number) {
    this.cartService.decreaseItem(id);
  }

  navigatePlaceOrder() {
    this.router.navigate(['place-order']);
  }
}