import { Component, Injector, Input, OnDestroy, OnInit } from '@angular/core';
import { ModalRequest, ModalType, ResponseObject } from 'src/app/models/common';
import { DeliveryTimingFilter } from 'src/app/models/delivery-timings';
import { DeliveryTimingsService } from 'src/app/services/api-services/delivery-timings.service';
import { PlaceOrderService } from 'src/app/services/api-services/place-order.service';
import { BaseComponent } from 'src/app/shared/base.component';

@Component({
  selector: 'app-mb-timing-selector',
  templateUrl: './mb-timing-selector.component.html',
  styleUrls: ['./mb-timing-selector.component.scss']
})
export class MbTimingSelectorComponent extends BaseComponent implements OnInit, OnDestroy {
  
  timingList: any[];
  dayList: any[] = [];
  selectedIndex: number = 0;
  selectedSlot: number = 0;
  selectedSlotItem: any = null;

  // orderStatus: typeof OrderStatus = OrderStatus;
  // camelCaseToSpacedText: typeof transformCamelToSpaces = transformCamelToSpaces;

  // orders: Order[];
  selectedId: number;

  constructor(protected injector: Injector, private services: DeliveryTimingsService,
    private placeOrderService: PlaceOrderService) {
    super(injector);
  }

  ngOnInit(): void {

    let filter: DeliveryTimingFilter = new DeliveryTimingFilter();
    this.services.getDeliveryTimingsList(filter).subscribe((res: ResponseObject<any[]>) => {
      this.timingList = res.Data ?? [];
      if (this.timingList != null) {
        let date: Date = (new Date());
        let ind = (date.getDay() < 6 ? date.getDay() + 1 : 0);
        let count = 4;
        let selected: boolean = false;
        while (count > 0) {
          if (ind > 6) ind = 0;
          let item = this.timingList.find(item => item.WeekDayId === ind); 
          this.dayList.push({
            DayName: item.DayName,
            WeekDayId: item.WeekDayId,
            Date: date,
            Title: count == 4 ? "Today" : count == 3 ? "Tomorrow" : null,
            TimeSlots: []
          });
          ind++;
          date = this.addDays(date, 1);
          count--;
        }
        this.selectedIndex = this.dayList[0].WeekDayId;
        this.timingList.forEach(item => {
          let itemIndex = this.dayList.findIndex(i => i.WeekDayId == item.WeekDayId);

          if (this.dayList[itemIndex] != undefined) {
            if (item.IsTimingActive && (this.selectedSlotItem == null || this.selectedSlotItem.WeekDayId > item.WeekDayId)) {
              this.selectedIndex = item.WeekDayId;
              this.selectedSlot = item.Id; 
              this.selectedSlotItem = item;
              console.log("selecting --- " + this.selectedSlotItem.WeekDayId)
            }
            this.dayList[itemIndex].TimeSlots.push(item);
          }
        })
        console.log("Selected Slot: " + this.selectedSlot);
      }
      this.timeSelected(this.selectedSlotItem);
    })
    
  }

  timeSelected(item: any, shouldClose: boolean = false) {
    this.selectedSlot = item.Id;
    // modalType: ModalType;
    // open: boolean;
    // data: Product | ProductVariant | Category | null;
    let request: ModalRequest = {
      modalType: ModalType.TimingSelection,
      open: false,
      data: item
    }

    this.utilities.closeModalDialog(request);
    this.placeOrderService.setSelectedTimeSlot(request.data);
  }
}
