import { Component, Injector, Input, NgZone, OnInit } from '@angular/core';
import { AbstractControl, FormGroup, Validators } from '@angular/forms';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { AddressesService } from 'src/app/services/api-services/address.service';
import { PlaceOrderService } from 'src/app/services/api-services/place-order.service';
import { Address, AddressFilter } from 'src/app/models/address';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { CartService } from 'src/app/services/cart.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { ModalType, PaymentType, ResponseObject, Result } from 'src/app/models/common';
import { ProductFilter, ProductVariant } from 'src/app/models/product';
import { Cart, CartProductView } from 'src/app/models/cart';
import { environment } from 'src/environments/environment';
import { Order } from 'src/app/models/order';
import { WalletService } from 'src/app/services/api-services/wallet.service';
import { Wallet } from 'src/app/models/wallet';

declare var Razorpay: any;
declare var UIkit: any;

@Component({
  selector: 'app-mb-place-orders',
  templateUrl: './mb-place-orders.component.html',
  styleUrls: ['./mb-place-orders.component.scss']
})
export class MbPlaceOrdersComponent extends BaseComponent implements OnInit {

  razorPay: any;

  currentWalletBalance: number;

  order: Order = new Order();

  selectedTimeSlot: any;

  productCartList: CartProductView[] = [];

  razorPayOptions: any = environment.razorPayOptions;

  addresses: Address[] = [];

  paymentMethod: number = PaymentType.OnlinePayment;

  razorPayModal = {
    ondismiss: this.razorPayDismiss.bind(this),
    escape: true,
    backdropclose: false
  };

  formInstance: FormGroup = this.fb.group({
    RelDay: [0, [Validators.required, Validators.max(1)]],
    TimePeriod: [1, [Validators.required]],
    IsDelivery: [true],
    UseWallet: [false],
  });

  get cartCount(): number {
    return this.cartService.getCartCount();
  }

  get relDay(): AbstractControl | null {
    return this.formInstance.get("RelDay");
  }

  get useWallet(): boolean {
    return this.formInstance.get("UseWallet")?.value;
  };

  get timePeriod(): AbstractControl | null {
    return this.formInstance.get("TimePeriod");
  }

  @Input() set active(_active: boolean) {
    if (_active) {
      this.ngOnInit();
    }
  }

  get cartTotal(): number {
    return this.productCartList.reduce((sum, item) => sum + (item.Quantity * item.ProductVariant.Price), 0);
  }

  get cartItems(): Cart[] {
    return this.cartService.getCartList();
  }

  get defaultAddress(): Address | null {
    if (this.addresses.length > 0) {
      let addresses = this.addresses.filter(i => i.IsDefault);
      if (addresses == null || addresses.length > 0) {
        return addresses[0];
      }
      else {
        return this.addresses[0];
      }
    }
    return null;
  }

  @Input()
  set index(ind: number) {
    this.formInstance.patchValue({ IsDelivery: ind == 0 });
    if (this.switcher) {
      setTimeout(() => {
        this.switcher.show(ind);
      }, 300);
    }
  }

  get index(): number {
    return this.formInstance.get("IsDelivery")?.value === true ? 0 : 1;
  }

  get switcher(): any {
    return UIkit.switcher('#switcher-panel');
  };

  constructor(injector: Injector,
    private authService: AuthenticationService,
    private addressService: AddressesService,
    private cartService: CartService,
    private productService: ProductsService,
    private walletService: WalletService,
    private zone: NgZone,
    private placeOrderService: PlaceOrderService) {
    super(injector);
  }

  ngOnInit(): void {

    if (this.switcher) {
      setTimeout(() => {
        this.switcher.show(this.index);
      }, 200);
    }

    let filter = new AddressFilter();
    filter.UserId = this.authService.currentUserValue.userId;
    this.addressService.getAddressList(filter).subscribe((res: ResponseObject<any>) => {
      console.log("Addresses: ")
      this.addresses = res.Data;
    });

    this.placeOrderService.orderTimeSlotStatus.subscribe((res: any) => {
      console.log("Status of selection: " + JSON.stringify(res));
      if (res != null) {
        this.selectedTimeSlot = res;
        this.timePeriod?.setValue(this.selectedTimeSlot.TimePeriodId);
        this.placeOrderService.setSelectedTimeSlot(null);
      }
    });

    this.loadProducts();
  }

  loadProducts() {

    if (this.cartItems.length == 0) {
      this.productCartList = [];
      return;
    }

    this.showSpinner();

    let filter: ProductFilter = new ProductFilter();
    filter.Ids = this.cartItems.map(i => i.ProductVariantId).join(',');
    this.productService.getProductVariantList(filter).subscribe((res: ResponseObject<ProductVariant[]>) => {
      let productCartList: CartProductView[] = [];
      let variantList = res.Data ?? [];
      variantList.forEach(vItem => {
        this.cartItems.forEach(cItem => {
          if (vItem.Id == cItem.ProductVariantId) {
            productCartList.push({
              Quantity: cItem.Quantity,
              ProductVariantId: cItem.ProductVariantId,
              ProductVariant: vItem
            })
          }
        });
      })
      this.productCartList = productCartList;
      this.hideSpinner();
    });
    this.walletService.getWalletList().subscribe(res => {
      let walletList: Wallet[] = res.Data;
      let currentBalance = 0;
      walletList.forEach(item => currentBalance += item.Amount);
      this.currentWalletBalance = currentBalance;
      if (this.currentWalletBalance > 0) this.formInstance.patchValue({ "UseWallet": true })
    });
  }

  submit() {

    this.showFullSpinner();

    let url = environment.apiUrl + "/order/initiate-order";

    this.order.RelDay = this.relDay?.value;
    this.order.TimePeriod = this.timePeriod?.value;
    this.order.IsDelivery = this.index == 0;
    this.order.OrderDate = new Date();
    this.order.PaymentMode = this.paymentMethod;
    this.order.Purchases = [];
    this.order.UseWallet = this.useWallet;

    let isDirectPlace = this.order.UseWallet && this.cartTotal <= this.currentWalletBalance;

    if (this.order.PaymentMode == PaymentType.CashOnDelivery || isDirectPlace) {

      this.order.Purchases = this.cartItems;
      this.placeOrder(this.order, () => {
        this.hideFullSpinner()
        this.cartService.clearList();
        this.orderPlacedSuccessfully();
      });

    } else {
      let sum = 0;
      this.order.Purchases.forEach(item => {
        let amount = (item.Price - ((item.Price * item.Discount / 100))) * item.Quantity;
        sum += amount;
      })
      let request = {
        UseWallet: this.useWallet,
        CartList: this.cartItems
      }
      this.apiRequestService.post(url, request).subscribe(res => {
        this.payment(res.Data.RazorPayOrderId, res.Data.Amount);
      });
    }
  }

  placeOrder(order: Order, callback?: Function) {

    let url = environment.apiUrl + "/order/place-order";
    this.apiRequestService.post(url, order).subscribe(res => {
      console.log(res);
      if (callback) {
        callback();
      }
    });
  }

  razorPayDismiss() {
    this.router.navigate(['/'], { replaceUrl: true });
  }

  razorPayHandler(response: any) {
    this.order.PaymentId = response.razorpay_payment_id;
    this.order.PaymentOrderId = response.razorpay_order_id;
    this.order.PaymentSignature = response.razorpay_signature;

    this.showFullSpinner()
    this.placeOrder(this.order, () => {
      this.cartService.clearList();
      this.orderPlacedSuccessfully();
    });
  }

  orderPlacedSuccessfully() {

    this.hideFullSpinner();

    // this.apiRequestService.showMessage({
    //   Message: "Your order Is Placed Successfully", Result: Result.Success,
    //   Data: 0,
    //   IsSuccess: true
    // })
    this.zone.run(() => {
      this.router.navigate(['order-success']);
    });

  }

  payment(orderId: string, amount: number) {

    this.hideFullSpinner();

    var ref = this;
    this.razorPayOptions.amount = amount;
    this.razorPayOptions.order_id = orderId;
    this.razorPayOptions.handler = this.razorPayHandler.bind(this);
    this.razorPayOptions.modal = this.razorPayModal;
    this.razorPayOptions.prefill = {
      name: "Sauban Sada",
      email: "sadasauban123@gmail.com",
      contact: "5858585858"
    };

    this.razorPay = new Razorpay(this.razorPayOptions);

    this.razorPay.modal = this.razorPayModal;

    this.razorPay.open();
  }

  openTimeSelectionModal() {
    let request = { open: false, modalType: ModalType.TimingSelection, data: null };
    this.utilities.openModalDialog(request);
  }
}
