import { Component, Input, OnInit } from '@angular/core';
import { ModalType } from 'src/app/models/common';
import { Product, ProductVariant } from 'src/app/models/product';
import { CartService } from 'src/app/services/cart.service';
import { UtilitiesService } from 'src/app/services/utilities.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-product-grids',
  templateUrl: './mb-product-grids.component.html',
  styleUrls: ['./mb-product-grids.component.scss']
})
export class MbProductGridsComponent implements OnInit {


  @Input() public product!: Product;


  get selectedIndex(): number {
    let selectedIndex = 0;
    for (let index = 0; index < this.product?.ProductVariants?.length; index++) {
      let count = this.cartService.getProductCartCount(this.product.ProductVariants[index].Id);
      if (count > 0) {
        selectedIndex = index;
        break;
      }
    }
    return selectedIndex;
  }

  get selectedProduct(): ProductVariant | undefined {
    if (this.product != null && this.product.ProductVariants.length > 0) {
      return this.product.ProductVariants[this.selectedIndex];
    }
    else return;
  }

  get cartCount(): number {
    let count = 0;
    if (this.product != null && this.product.ProductVariants?.length > 0) {
      count = this.cartService.getProductCartCount(this.product.ProductVariants[this.selectedIndex].Id);
    }
    return count;
  }

  imgUrl: string = environment.uploadsUrl;

  constructor(private utilities: UtilitiesService, private cartService: CartService) { }

  ngOnInit(): void {
  }

  openProductOverView() {
    let request = { open: false, modalType: ModalType.ProductOverview, data: this.product };
    // this.utilities.openModalDialog(request);
  }

  openProductVariants(event: any) {
    event.stopPropagation();
    let request = { open: false, modalType: ModalType.ProductVariant, data: this.product };
    this.utilities.openModalDialog(request);
  }

  addToCart(event: any) {
    event.stopPropagation();
    this.cartService.addItem(this.product.ProductVariants[this.selectedIndex].Id);
  }

  removeFromCart(event: any) {
    event.stopPropagation();
    this.cartService.decreaseItem(this.product.ProductVariants[this.selectedIndex].Id);
  }

}
