import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ResponseObject } from 'src/app/models/common';
import { Order, OrderStatus } from 'src/app/models/order';
import { OrderService } from 'src/app/services/api-services/order.service';

declare var UIkit: any;

@Component({
  selector: 'app-mb-order-cancel-reason',
  templateUrl: './mb-order-cancel-reason.component.html',
  styleUrls: ['./mb-order-cancel-reason.component.scss']
})
export class MbOrderCancelReasonComponent implements OnInit {

  name: string = "cancelled-reason";
  modal: any;

  constructor(private orderService: OrderService,
    private router: Router, 
    private spinner: NgxSpinnerService) { }

  @Input()
  order?: Order;
  
  ngOnInit(): void {
  }
  

  cancelOrder() {
    this.modal = UIkit.modal('#' + this.name);

    let order = Object.assign({}, this.order);
    order.OrderState = OrderStatus.Cancelled;
    this.spinner.show();
    this.orderService.updateOrderStatus(order).subscribe((res: ResponseObject<any>) => {
      this.spinner.hide();
      this.modal.hide();
      var ele = document.getElementById(this.name);
      ele?.remove();
      this.router.navigate(["/my-orders"]);
    });
  }
}
