import { Location } from '@angular/common';
import { Component, Injector, OnInit } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
    selector: 'app-mb-login',
    templateUrl: './mb-login.component.html',
    styleUrls: ['./mb-login.component.scss']
})
export class MbLoginComponent extends BaseComponent implements OnInit {

    submitted: boolean = true;

    assetsUrl: string = environment.assetsUrl;
    
    showPassword: boolean = false;

    formInstance: FormGroup = this.fb.group({
        UserName: ['', [Validators.required]],
        Password: ['']
    })

    constructor(protected injector: Injector, private location: Location,
        private authenticationService: AuthenticationService) {
        super(injector);
    }

    ngOnInit(): void {
        this.spinnerOptions.bdColor = 'rgba(0,0,0,0.5)';
        this.spinnerOptions.color = 'rgba(256,256,256,0.8)';
    }

    back() {
        this.location.back();
    }

    get email() {
        return this.formInstance.get('UserName');
    }

    get password() {
        return this.formInstance.get('Password');
    }

    visibility() {
        this.showPassword = !this.showPassword;
    }

    submitForm() {

        this.submitted = true;

        if (this.formInstance.invalid) {
            return;
        }

        this.showSpinner();
        let pass = this.password?.value == null ? '' : this.password?.value;
        this.authenticationService.login(this.email?.value, pass)
            .pipe(first())
            .subscribe(
                (user) => {
                    this.formInstance.reset();
                    if (user.requireOTP == "True") {
                        let url = this.router.url.replace("/login", "/otp");
                        this.router.navigateByUrl(url);
                    } else {

                    }
                    this.hideSpinner();
                },
                (error: any) => {
                    setTimeout(() => {
                        this.hideSpinner();
                    }, 500);
                });
    }
}
