import { Component, Input, OnInit } from '@angular/core';
import { Product, ProductVariant } from 'src/app/models/product';
import { CartService } from 'src/app/services/cart.service';
import { UtilitiesService } from 'src/app/services/utilities.service';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-product-variant',
  templateUrl: './mb-product-variant.component.html',
  styleUrls: ['./mb-product-variant.component.scss']
})
export class MbProductVariantComponent implements OnInit {

  imgUrl: string = environment.uploadsUrl;

  @Input()
  product: Product;
  
  get cartCount(): number[] {
    let count: number[] = [];
    if (this.product != null && this.product.ProductVariants?.length > 0) {
      for (let item of this.product.ProductVariants) {
        count.push(this.cartService.getProductCartCount(item.Id));
      }
    }
    return count;
  }

  constructor(private utilities: UtilitiesService, private cartService: CartService) { }

  ngOnInit(): void {
  }

  // openProductOverView() {
  //   let request: ModalRequest = { open: false, modalType: ModalType.ProductVariant, data: null };

  //   this.utilities.closeModalDialog(request);

  //   request = { open: false, modalType: ModalType.ProductOverview, data: this.product }
  //   this.utilities.openModalDialog(request);
  // }
 
  addToCart(variant: ProductVariant) {
    this.cartService.addItem(variant?.Id ?? 0);
  }

  removeFromCart(variant: ProductVariant) {
    this.cartService.decreaseItem(variant?.Id ?? 0);
  }

}
