import { Component, EventEmitter, Injector, OnInit, Output } from '@angular/core';
import { Category } from 'src/app/models/category';
import { ResponseObject } from 'src/app/models/common';
import { CategoriesService } from 'src/app/services/api-services/categories.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-home-categories',
  templateUrl: './mb-home-categories.component.html',
  styleUrls: ['./mb-home-categories.component.scss']
})
export class MbHomeCategoriesComponent extends BaseComponent implements OnInit {
  
  imgUrl: string = environment.uploadsUrl;

  categoryList: Category[] = []; 

  @Output()
  categoryClicked: EventEmitter<Category> = new EventEmitter<Category>();

  categoryCalicked(item: Category) {
    this.categoryClicked.emit(item);
  }

  constructor(protected injector: Injector, 
    private categoryService: CategoriesService) {
    super(injector);
  }

  ngOnInit(): void {
    let filter: any;
    this.categoryService.getCategoryList(filter).subscribe((res: ResponseObject<Category[]>) => {
      let data = res.Data ?? [];
      data.forEach(item => {
        item.CategoryImage = item.CategoryImage.replace('~', '');
      })
      this.categoryList = data;

      console.log(this.categoryList);
    })
  }

}