import { Component, Injector, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, Validators } from '@angular/forms';
import { AuthenticationService } from 'src/app/auth/_services/authentication.service';
import { ResponseObject } from 'src/app/models/common';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-otp',
  templateUrl: './mb-otp.component.html',
  styleUrls: ['./mb-otp.component.scss']
})
export class MbOtpComponent extends BaseComponent implements OnInit {
  fb: any;
  apiRequestService: any;

  get phoneNumber(): string {
    return this.authService.currentUserValue?.userName;
  }

  get isRequiredOTP(): boolean {
    return this.authService.currentUserValue?.requireOTP === "True";
  }

  formInstance: FormGroup = this.fb.group({
    OtpCode: ['', [Validators.required]]
  })

  constructor(protected injector: Injector, private location: Location,
    private authService: AuthenticationService) {
    super(injector);
  }

  ngOnInit(): void {
    this.spinnerOptions.bdColor = 'rgba(0,0,0,0.5)';
    this.spinnerOptions.color = 'rgba(256,256,256,0.8)';
  }

  ngOnViewUpdated() {

  }

  back() {
    this.location.back();
  }


  submitOtp() {
    let otp = this.formInstance.get("OtpCode")?.value;
    this.showFullSpinner();
    this.apiRequestService.get(environment.apiUrl + '/auth/verify-otp/' + otp).subscribe((res: ResponseObject<any>) => {
      this.formInstance.reset();
      this.hideFullSpinner();
      this.authService.updateRequireOTP(false);
      this.router.navigate(['/'], { replaceUrl: true });
    })
  }
}
