import { Component, Injector, Input, OnInit } from '@angular/core';
import { WalletService } from 'src/app/services/api-services/wallet.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { Location } from '@angular/common';
import { Wallet } from 'src/app/models/wallet';
import { BankAccountService } from 'src/app/services/api-services/bank-accounts.service';
import { RedeemRequestService } from 'src/app/services/api-services/redeem-request.service';
import { RedeemRequest } from 'src/app/models/redeem-request';
import { BankAccount } from 'src/app/models/bank-account';
import { FormGroup } from '@angular/forms';

declare var UIkit: any;

@Component({
  selector: 'app-mb-wallet-redeem',
  templateUrl: './mb-wallet-redeem.component.html',
  styleUrls: ['./mb-wallet-redeem.component.scss']
})
export class MbWalletRedeemComponent extends BaseComponent implements OnInit {

  @Input() modalId: string = 'wallet-redeeem-request-modal';

  _switcher: any;
  walletList: Wallet[];
  redeemRequestList: RedeemRequest[] = [];
  currentBalance: number;
  bankAccounts: BankAccount[];
  redeemRequestDetails: RedeemRequest | null = null;


  isAddOrEdit: boolean = false;
  isEditMode: boolean = false;

  currentSwitcherIndex: number = 0;

  bankAccountForm = this.fb.group({
    BankAccountId: [],
  })

  constructor(protected injector: Injector, private walletService: WalletService,
    private bankAccountService: BankAccountService, private redeemRequestService: RedeemRequestService,
    private _location: Location) {
    super(injector);
  }

  ngOnInit(): void {
 
    this.loadWalletInfo();
    this.loadRedeemHistory();
    this.loadBankAccounts();

    this.currentSwitcherIndex = 0;
  }

  setValue(bankAccount: BankAccount) {
    this.bankAccountForm.patchValue({ BankAccountId: bankAccount.Id });
  }

  get switcher(): any {
    if (this._switcher == null) {
      this._switcher = UIkit.switcher('#data-switcher', { animation: false })
    }
    return this._switcher;
  }

  switch(index: number) {
    this.currentSwitcherIndex = index;
    setTimeout(() => {
      this.switcher.show(index);
    })
  }

  _createRequestDialog: any;
  
  get redeemRequestDialog(): any {
    if (this._createRequestDialog == null) {
      this._createRequestDialog = UIkit.modal('#redeem-request')
    }
    return this._createRequestDialog;
  }
  
  openRedeemRequestDialog() {
    setTimeout(() => {
      this.redeemRequestDialog.show();
    });
  }

  loadBankAccounts() {
    this.bankAccountService.getBankAccountList().subscribe((res: { Data: BankAccount[]; }) => {
      this.bankAccounts = res.Data;
      if (this.bankAccounts?.length > 0) {
        this.bankAccountForm.patchValue({ BankAccountId: this.bankAccounts[0].Id });
      }
    })
  }

  addAddress() {

  }

  close() {
    //this.formInstance.reset();
    let url = this.router.url.split("/")[0];
    this.router.navigateByUrl(url);
  }

  navigateBack() {
    if (this.currentSwitcherIndex != 0) {
      this.switch(0);
    } else {
      this._location.back();
    }
  }

  loadWalletInfo() {
    this.walletService.getWalletList().subscribe((res: { Data: Wallet[]; }) => {
      this.walletList = res.Data;
      this.currentBalance = 0;
      this.walletList.forEach(item => this.currentBalance += item.Amount);

    })
  }

  loadRedeemHistory() {
    this.redeemRequestService.getRedeemRequestList().subscribe(res => {
      this.redeemRequestList = res.Data;
      this.redeemRequestList = [
        {
          Id: 1,
          RequestedByUserId: '4345',
          BankAccountNumber: '43453453',
          BankAccountId: 22,
          Amount: 100,
          RequestState: 11,
          RequestDateTime: new Date(),
          RequestCompletionDateTime: new Date()
        },
        {
          Id: 2,
          RequestedByUserId: '13455',
          BankAccountNumber: '345345',
          BankAccountId: 22,
          Amount: 200,
          RequestState: 12,
          RequestDateTime: new Date(),
          RequestCompletionDateTime: new Date()
        }
      ];
    });
  }

  navigateBankAccounts() {
    this.router.navigate([{ outlets: { action: ['bank-accounts'], primary: [] } }]);
  }

  editBankAccount() {
    this.router.navigate([{ outlets: { action: ['edit-bank-account'], primary: [] } }]);
  }

  createRedeemRequest() {

    this.redeemRequestService.createRedeemRequest(<RedeemRequest><unknown>this.bankAccountForm.value).subscribe(res => {

    });
  }
}
