import { Component, Injector, Input, OnInit } from '@angular/core';
import { Category } from 'src/app/models/category';
import { ResponseObject } from 'src/app/models/common';
import { Product, ProductFilter } from 'src/app/models/product';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { BottomNavItem } from 'src/app/shared/ngx-bottom-nav/models/bottom-nav';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mobile-home',
  templateUrl: './mobile-home.component.html',
  styleUrls: ['./mobile-home.component.scss']
})
export class MobileHomeComponent extends BaseComponent implements OnInit {

  displayCart: boolean = false;

  featuredProducts: Product[] = [];
  suggestedProducts: Product[] = [];

  currentRoute: string = "";

  constructor(protected injector: Injector,
    private productService: ProductsService) {

    super(injector);
  }

  ngOnInit(): void {

    let filter: ProductFilter = new ProductFilter();
    filter.PageSize = 6;
    this.productService.getProductList(filter).subscribe((res: ResponseObject<Product[]>) => {

      let data = res.Data ?? [];

      if (!environment.production) {
        console.log("Products Loaded: ");
        console.log(data);
      }
      this.featuredProducts = this.processProductList(data);
    });

    filter.PageSize = 6;
    this.productService.getProductList(filter).subscribe((res: ResponseObject<Product[]>) => {

      let data = res.Data ?? [];

      if (!environment.production) {
        console.log("Products Loaded: ");
        console.log(data);
      }
      this.suggestedProducts = this.processProductList(data);
    });
  }

  onCategoryClicked(event: Category) {
    this.router.navigate(['/categories', event.Id]);
  }

  reachedEnd() {
  }

  processProductList(list: Product[]): Product[] {
    let _res: Product[] = []
    list.forEach(item => {
      let images: string[] = [];
      item.ProductImages.forEach(img => {
        images.push(img.replace('~', ''));
      });
      item.ProductImages = images
      _res.push(item);
    });
    return _res;
  }
}
