import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-mb-header-nav',
  templateUrl: './mb-header-nav.component.html',
  styleUrls: ['./mb-header-nav.component.scss']
})
export class MbHeaderNavComponent implements OnInit {


  @Output()
  onBackClick: EventEmitter<any> = new EventEmitter<any>();


  constructor(protected router: Router, private location: Location) { }

  @Input() title: string = 'Modern Mart'
  @Input() hasActions: boolean = true;
  @Input() hasNav: boolean = true;
  @Input() isTitleCenter: boolean = false;
 
  ngOnInit(): void {
  }

  navigateBack() {
    if (this.onBackClick.observers.length == 0) {
      this.location.back();
    } else{
      this.onBackClick.emit();
    }
  }

}
