import { Component, Injector, OnInit } from '@angular/core';
import { enumToKeyValueArray, OrderStatus, ResponseObject } from 'src/app/models/common';
import { Order, OrderDetail, OrderFilter } from 'src/app/models/order';
import { OrderService } from 'src/app/services/api-services/order.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-order-details',
  templateUrl: './mb-order-details.component.html',
  styleUrls: ['./mb-order-details.component.scss']
})
export class MbOrderDetailsComponent extends BaseComponent implements OnInit {

  assetsUrl: string = environment.assetsUrl;
  
  public orderStatuses: any = enumToKeyValueArray(OrderStatus);
    
  id: number;

  order?: Order;
 
  get orderNotCancelledOrRejected(): boolean {
    return !(this.order?.OrderState == 6 || this.order?.OrderState == 7)
  }

  constructor(protected injector: Injector, protected orderService: OrderService) { super(injector); }

  ngOnInit(): void {
   
    let pos = this.router.url.lastIndexOf('/') + 1;
    let orderQuery = this.router.url.substring(pos, this.router.url.length);
    if (!isNaN(<any>orderQuery)) {
      let id = parseInt(orderQuery);
      this.orderService.getOrderInfo(id).subscribe((res: ResponseObject<Order>) => {
        console.log(res.Data);
        let order = res.Data;
        order?.Purchases.forEach((item: OrderDetail) => {
          item.ProductImage.ImageUrl = item.ProductImage.ImageUrl.replace('~', '');
        })
        this.order = order;
        this.initOrderStatus(this.order?.OrderState ?? 0);
      })
    } else {
      this.apiRequestService.showMessage(<ResponseObject<any>>{
        Message: "Invalid Order Id"
      });
      this.router.navigate([{ outlets: { action: ['my-orders'], primary: [] } }]);
    }
  }

  initOrderStatus(orderStatus: OrderStatus) {
    let found: boolean = false;
    let excludeList = [OrderStatus.All, OrderStatus.Placed, OrderStatus.Cancelled, OrderStatus.Rejected];
    for (let status of this.orderStatuses) {
      status.done = false;
      status.done = !found;
      status.show = !excludeList.includes(status.id);
      if (status.id == orderStatus)
        found = true;
    }
  }
 
  actualTotalPrice(order: any[] | undefined) {
    let sum = 0;
    order?.forEach(i => sum += i.Price * i.TotalQuantity);
    return sum;
  }
}
