import { Component, Injector, OnInit } from '@angular/core';
import { OfferBanner } from 'src/app/models/offer';
import { OfferService } from 'src/app/services/api-services/offers.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-mb-story-view',
  templateUrl: './mb-story-view.component.html',
  styleUrls: ['./mb-story-view.component.scss']
})
export class MbStoryViewComponent extends BaseComponent implements OnInit {

  uploadsUrl: string = environment.uploadsUrl;

  currentBanner: OfferBanner | null;
  progress: number = 0;
  interval: any;

  constructor(protected injector: Injector, protected offerSeervice: OfferService) {
    super(injector);

  }

  ngOnInit(): void {
    this.offerSeervice.currentBanner.subscribe((res) => {
      this.currentBanner = res;
      if (res == null) {

        this.router.navigate(['/'])
      } else {
        this.progress = 1;

        this.interval = setInterval(() => {
          this.progress += 1.5;

          if (this.progress >= 100) {
           clearInterval(this.interval)
           this.router.navigate(['/'])
          }
        }, 100)
      }
    });

  }

}
