import { Component, Injector, Input, OnInit } from '@angular/core';
import { FormGroup, Validators } from '@angular/forms';
import { ParamMap, Params, RoutesRecognized } from '@angular/router';
import { map, switchMap } from 'rxjs/operators';
import { Address } from 'src/app/models/address';
import { ResponseObject, Result } from 'src/app/models/common';
import { AddressesService } from 'src/app/services/api-services/address.service';
import { BaseComponent } from 'src/app/shared/base.component';


@Component({
  selector: 'app-mb-add-address',
  templateUrl: './mb-add-address.component.html',
  styleUrls: ['./mb-add-address.component.scss']
})
export class MbAddAddressComponent extends BaseComponent implements OnInit {

  submitted: boolean = false;

  path: string = 'edit-address'

  @Input() public set routeData(data: string) {
    let num = parseInt(data);
    if (!isNaN(num)) {
      this.isEditMode = true;
      this.addressService.getAddressInfo(num).subscribe((res: ResponseObject<any>) => {
        this.formInstance.patchValue(res.Data);
      })
    }
  }

  routerSubscription: any;

  @Input() modalId: string;

  @Input() set active(_active: boolean) {
  }


  private _isGPS: boolean = false;

  set isGPS(_isGPS: boolean) {
    this._isGPS = _isGPS;
    if (_isGPS) {
      this.clearAddressValidators();
      this.setGPSValidators();
    }
    else {
      this.setAddressValidators();
      this.clearGPSValidators();
    }
  };

  get isGPS(): boolean {
    return this._isGPS;
  }

  formInstance: FormGroup = this.fb.group({
    Id: [''],
    FullName: ['', [Validators.required]],
    PhoneNumber: ['', [Validators.required]],
    AreaCode: [''],
    AddressLine1: [''],
    AddressLine2: [''],
    LandMark: [''],
    Country: ['India'],
    GPS: [''],
    IsDefault: [false]
  })

  get fullName() { return this.formInstance.get("FullName") }
  get phoneNumber() { return this.formInstance.get("PhoneNumber") }
  get areaCode() { return this.formInstance.get("AreaCode") }
  get addressLine1() { return this.formInstance.get("AddressLine1") }
  get addressLine2() { return this.formInstance.get("AddressLine2") }
  get landMark() { return this.formInstance.get("LandMark") }
  get country() { return this.formInstance.get("Country") }
  get gps() { return this.formInstance.get("GPS") }

  isEditMode: boolean = false;

  constructor(protected injector: Injector, private addressService: AddressesService) {
    super(injector);
    this.errorIntrcptr.httpError$.subscribe(res => {
      this.messageShown = false;
      if (res.Result == Result.NotFound) {
        this.router.navigateByUrl('/address');
      }
    })
  }

  ngOnInit(): void {
    
    let pos = this.router.url.lastIndexOf('/') + 1;
    let orderQuery = this.router.url.substring(pos, this.router.url.length);
    if (!isNaN(<any>orderQuery)) {
    
      let id = parseInt(orderQuery);
      this.isEditMode = true;
      this.addressService.getAddressInfo(id).subscribe((res: ResponseObject<any>) => {
        
        this.formInstance.patchValue(res.Data);
      })
    }
    
    this.setAddressValidators();
  }

  setAddressValidators() {
    this.addressLine1?.setValidators([Validators.required]);
    this.addressLine2?.setValidators([Validators.required]);
    this.landMark?.setValidators([Validators.required]);
    this.country?.setValidators([Validators.required]);
  }

  clearAddressValidators() {
    this.addressLine1?.clearValidators();
    this.addressLine2?.clearValidators();
    this.landMark?.clearValidators();
    this.country?.clearValidators();
  }

  setGPSValidators() {
    this.gps?.setValidators([Validators.required]);
  }

  clearGPSValidators() {
    this.gps?.clearValidators();

  }

  onSubmit() {
    if (this.formInstance.valid) {
      this.showFullSpinner();
      if (this.isEditMode) {
        let address: Address = this.formInstance.value
        this.addressService.editAddressInfo(address).subscribe(() => {
          this.hideFullSpinner();
          this.close();
        });
      } else {
        let address: Address = this.formInstance.value
        this.addressService.addAddressInfo(address).subscribe(() => {
          this.hideFullSpinner();
          this.close();
        });
      }
    }
  }

  close() {
    this.formInstance.reset();
    let url = this.router.url.split("/")[0];
    this.router.navigateByUrl(url);
  }
}
