import { Component, Injector, OnInit } from '@angular/core';
import { ResponseObject } from 'src/app/models/common';
import { Product, ProductVariant } from 'src/app/models/product';
import { ProductsService } from 'src/app/services/api-services/products.service';
import { CartService } from 'src/app/services/cart.service';
import { BaseComponent } from 'src/app/shared/base.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent extends BaseComponent implements OnInit {

  assetsUrl: string = environment.assetsUrl;

  productId: number = 0;

  product: Product | null = null;


  imgUrl: string = environment.uploadsUrl;
  selectedIndex: number = 0;

  get selectedProduct(): ProductVariant | undefined {
    if (this.product != null && this.product.ProductVariants.length > 0) {
      return this.product.ProductVariants[this.selectedIndex];
    }
    else return;
  }

  get cartCount(): number[] {
    let count: number[] = [];
    if (this.product != null && this.product.ProductVariants?.length > 0) {
      for (let item of this.product.ProductVariants) {
        count.push(this.cartService.getProductCartCount(item.Id));
      }
    }
    return count;
  }


  constructor(protected injector: Injector, private cartService: CartService,
    private productService: ProductsService) {
    super(injector);
  }

  ngOnInit(): void {
    this.route.params.subscribe(map => {
      this.productId = parseInt(map["id"] ?? '-1');

      this.productService.getProductInfo(this.productId).subscribe((res: ResponseObject<Product>) => {
        let arr = Object.assign([], res.Data?.ProductImages);
        if (res.Data != null) {
          res.Data.ProductImages = [];
          arr.forEach((item: string) => {
            var str = item.replace('~/', '');
            res.Data?.ProductImages.push(str);
          })
        } 
        this.product = res.Data ?? null;
      })
    })
  }


  addToCart() {
    this.cartService.addItem(this.selectedProduct?.Id ?? 0);
  }

  removeFromCart() {
    this.cartService.decreaseItem(this.selectedProduct?.Id ?? 0);
  }

}
