import { transition, trigger, style, query, group, animateChild, animate, state } from "@angular/animations";

export const openCloseAnimation = trigger('openClose', [
    transition(':enter', [
        style({
            position: 'relative',
            opacity: 0.7, left: '100%'
        }),
        animate('300ms', style({
            position: 'relative', opacity: 1, left: '0px'
        }))
    ]
    ),
    transition(':leave',
        [style({ opacity: 1, left: '100%' }),
        animate('300ms', style({ opacity: 0, left: '0px' }))]
    )
]);

export const fadeInOutAmination = trigger('fadeInOut', [
    transition(':enter', [
        style({ 
            opacity: 0.5, 
        }),
        animate('300ms', style({  opacity: 1,  }))
    ]
    ),
    transition(':leave',
        [style({ opacity: 1,  }),
        animate('300ms', style({  opacity: 0,  }))]
    )
]);
